.artworks-page {
    width: 98%;
    margin: auto;
}

.artworks-grid {
    /* max-width: 1200px; */
    margin: 0 auto;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(4, minmax(300px, 1fr));
}

.tabs-container {
    box-sizing: border-box;
    margin: 0px 0px 24px;
    min-width: 0px;
    -webkit-box-pack: justify;
    justify-content: space-between;
    align-items: flex-end;
    min-height: 32px;
    display: grid;
    gap: 16px;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-color: var(--theme-ui-colors-black-100, #000000);
    padding-bottom: 16px;
}

.tabs-container .tabs {
    box-sizing: border-box;
    margin: 0px;
    min-width: 0px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
}

.tabs-container .tabs .item {
    box-sizing: border-box;
    margin: 0px 8px 0px 0px;
    min-width: 0px;
    cursor: pointer;
    color: #ffffffa6;
    border-radius: 6px;
    padding: 4px 10px 6px;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    font-weight: 600;
    font-size: 14px;
}

.tabs-container .tabs .item.active {
    background-color:#4e61ff;
    color: #fff;
}

.tabs-container .tabs .item .text {

   
}

@media screen and (min-width: 40em) {
    .tabs-container .tabs .item {
        padding-left: 12px;
        padding-right: 12px;
    }
}

@media screen and (min-width: 52em) {
    .artworks-page {
        width: 80%;
        margin: auto;
    }
    .tabs-container {
        min-height: 38px;
        display: flex;
    }
    .tabs-container .tabs .item .text {
        font-size: 16px;
    }
}