.right-nav{
    margin: 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    flex-direction: row;
    -webkit-box-align: center;
    align-items: center;
}

.create-btn{
    border-color: transparent;
    color: rgb(45, 129, 255);
    background: rgba(45, 129, 255, 0.15);
    margin-right: 8px;
    display: flex;
    flex-flow: row nowrap;
    height: 40px;
    padding-left: 22px;
    padding-right: 22px;
    min-width: auto;
    border: 1px solid transparent;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    border-radius: 40px;
    font-size: 14px;
    font-weight: 900;
    font-family: inherit;
    transition: all 0.12s ease-in-out 0s;
    transform-origin: center center;
    user-select: none;
    cursor: pointer;
}
.create-btn:hover{
    color: rgba(45, 129, 255, 0.9);
    background: rgba(45, 129, 255, 0.2);
}

.create-btn:active{
    color: rgba(45, 129, 255, 0.9);
    background: rgba(45, 129, 255, 0.2);
}
.create-btn:active{
    transform: scale(0.95);
}

.user-container{
    margin: 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    position: relative;
}

.user-container .user-btn{
    text-decoration: none;
    color: rgb(45, 129, 255);
    border-radius: 40px;
    height: 40px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    -webkit-box-align: center;
    align-items: center;
    padding-left: 20px;
    padding-right: 55px;
    -webkit-box-pack: center;
    justify-content: center;
    box-sizing: content-box;
    transition: all 0.12s ease-in-out 0s;
    transform-origin: center center;
    background-color: #121212;
}


.user-container .user-btn-content{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 19.32px;
    margin: 0;
    
}
.user-container .user-btn-content h6{
    margin: 0;
    text-decoration: none;
    font-size: 14px;
    line-height: 19.32px;
    font-weight: 900;
    color: rgb(255, 255, 255);
}

.user-container .userAvatar{
    position: absolute;
    top: 1px;
    right: 1px;
    bottom: 1px;
}
.user-container .userAvatar .userAvatarContainer{
    
}
.user-container .userAvatar .userAvatarContainer img{
    height: 40px;
    width: 40px;
    border-radius: 50px;
}
