.header-container {
    position: relative;
    height: 300px;
}

.cover-container {
    height: 400px;
    width: 100%;
    position: absolute;
    top: -150px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.avatar-container {
    position: absolute;
    bottom: 0;
    left: 75px;
}

.avatar-container .avatar-img {
    height: 150px;
    width: 150px;
    ;
    border-radius: 100px;
}

.creator-content {
    margin-top: 60px;
}

.creator-content-withSidebar {
    box-sizing: border-box;
    margin: 0px auto;
    min-width: 0px;
    width: 100%;
    max-width: 1600px;
    padding-left: 24px;
    padding-right: 24px;
    flex: 1 1 0%;
    display: grid;
    gap: 32px;
}

.creator-profile {}

.creator-profile .publicAddress {
    font-weight: 400;
    letter-spacing: 0.6px;
    padding-left: 12px;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    cursor: pointer;
}

.creator-profile .publicAddress:hover {
    box-shadow: 0px 8px 15px rgb(0 0 0 / 25%);
    transform: translateY(-2px);
}

.creator-profile .fullName .text {
    margin: 0px 0px 5px;
    font-weight: 600;
    letter-spacing: -0.05em;
    font-size: 36px;
    line-height: 1.1;
}

.creator-profile .username .text-colored {
    line-height: 1.2;
    margin: 0px;
    background: linear-gradient(110.78deg, rgb(118, 230, 80) -1.13%, rgb(249, 214, 73) 15.22%, rgb(240, 142, 53) 32.09%, rgb(236, 81, 87) 48.96%, rgb(255, 24, 189) 67.94%, rgb(26, 75, 255) 85.34%, rgb(98, 216, 249) 99.57%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.creator-profile .follow-container {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    margin: 16px auto;
}

.creator-profile .follow-container .folowing {
    margin: 0px 24px 0px 0px;
    min-width: 0px;
    cursor: pointer;
}

.creator-profile .follow-container .value {
    font-weight: 600;
    /* color: #000000; */
    font-size: 24px;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
}

.creator-profile .follow-container .text {
    font-weight: 600;
    color: #7F7F7F;
    font-size: 18px;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
}

.creator-profile  .follow-button .btn {
    text-align: center;
    font-weight: 600;
    padding: 8px 16px;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    cursor: pointer;
    outline: none;
    background-color: transparent;
     color: #fff; 
    border-width: 2px;
    border-style: solid;
    border-image: initial;
    border-color: #E6E6E6;
    min-width: 130px;
    font-size: 18px;
    border-radius: 999px;
    min-height: 56px;
    line-height: 1.25;
}

.creator-profile .follow-button .btn:hover {
    background-color:#000000;
    color: #FFFFFF;
    box-shadow: rgb(0 0 0 / 25%) 0px 8px 15px;
    transform: translateY(-2px);
}



.creator-artwork-grid{
    margin: 0 auto;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(3, minmax(300px, 1fr));
}

.center-container{
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

@media screen and (min-width: 72em) {
    .creator-content-withSidebar {
        gap: 64px;
        grid-template-columns: minmax(350px, 1fr) 3fr;
    }
    .creator-profile .fullName .text {
        font-size: 46px;
    }
    .creator-profile .username .text-colored {
        font-size: 36px;
    }
}

@media screen and (min-width: 52em) {
    .creator-content-withSidebar {
        grid-template-columns: minmax(280px, 1fr) 3fr;
    }
    .creator-profile .fullName .text {
        margin-bottom: 16px;
    }
    .creator-profile .follow-container .button .btn {
        padding: 16px 24px;
    }
}

@media screen and (min-width: 40em) {
    .creator-profile .username .text-colored {
        font-size: 24px;
        letter-spacing: -0.035em;
    }
    .creator-profile .follow-container .button .btn {
        padding-left: 16px;
        padding-right: 16px;
    }
}