:root {
  --primary-color: #4e61ff
}

.dark-theme .card {
 color: #fff;
}

.card {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  text-decoration: none;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: rgb(0 0 0 / 10%) 0px 10px 20px;
}

.card-artwork {}

.card-artwork .media {
  height: 400px
}

.card-artwork .media .img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.card-artwork .media .video {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.card-artwork .content {
  padding: 24px;
}

.card-artwork .content .title {}

.card-artwork .content .title p {
  font-size: 24px;
  font-weight: 600;
  margin: 0px;
}

.card-artwork .content .creator-container {
  display: flex;
  padding: 5% 0px;
  align-items: center;
}

.card-artwork .content .creator-avatar {}

.card-artwork .content .creator-name {
  padding-left: 5px;
  font-weight: 600;
}

.card-artwork .content .creator-name p {}

.card-artwork .aution-container {
  padding: 24px;
  display: flex;
  justify-content: space-between;
  background: var(--primary-color);
  color: #fff;
  border-radius: 0 0 8px 8px;
}

.card-artwork .aution-container .aution-details {}

.card-artwork .aution-container .faded-text {
  margin-bottom: 8px;
  line-height: 1;
  color: #CCCCCC;
  font-size: 18px;
  font-weight: 600;
}

.card-artwork .aution-container .bold-text {
  margin-bottom: 8px;
  line-height: 1;
  font-size: 18px;
  font-weight: 600;
  border-radius: 0 0 10px 10px;
}

.card-artwork .aution-container .aution-timmer {}


.card-collection {
  position: relative;
  height: 300px;
  width: 300px;

}
.card-collection .media {
  height: 300px;
  width: 300px;
}

.card-collection .content {
  background: #121212a1;
  position: absolute;
  width: 100%;
  bottom: 0;
  color: #fff;
  transform: translateY(100px);
  transition:  2s linear 2s;
  display: none;
}
.card-collection .content .creator-container{
  display: flex;
  align-items: center;
}
.card-collection .content .creator-container .creator-name{
  padding-left: 8px;
}

.card-collection:hover .content{
  display: block;
  transform: translateY(0px);

}


.hot-bids  {
  padding: 2%;

}

.hot-bids .galllary {

}

.hot-bids .galllary {
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(4, minmax(300px, 1fr));
}