:root {
  --bg: #000;
  --bg-accent: #484a4d;
  --text-color: #000;
  --nav-size: 60px;
  --border: 1px solid #474a4d;
  --border-radius: 8px;
  --speed: 500ms;
  --primary-color: #4e61ff
}

.wallet {
  margin: 0px;
  padding: 10px 0px;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex: 1 1 auto;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
  flex-direction: row;
  -webkit-box-align: start;
  align-items: start;
  -webkit-box-flex: 1;
}

.wallet .icon {
  margin: 0px;
  padding: 0px;
  -webkit-box-align: stretch;
  align-items: stretch;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex-basis: auto;
  flex-direction: column;
  flex-shrink: 0;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  border-radius: 50%;
  background: rgb(170, 187, 255);
  width: 40px;
  height: 40px;
  margin-right: 12px;
}

.wallet .wallet-content {
  margin: 0px;
  padding: 0px;
  -webkit-box-align: stretch;
  align-items: stretch;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
  -webkit-box-flex: 1;
}

.wallet .wallet-content .title {
  flex-direction: row;
  margin-bottom: 2px;
  text-align: start;
}

.wallet .wallet-content .balance {
  font-size: 14px;
  line-height: 19.32px;
  font-weight: 900;
}

.wallet .wallet-content .balance span {
  margin-right: 8px;
}

.dropdown {
  position: absolute;
  top: 58px;
  width: 278px;
  transform: translateX(-45%);
  background-color: var(--bg);
  border: var(--border);
  border-radius: var(--border-radius);
  padding: 1rem;
  overflow: hidden;
  transition: height var(--speed) ease;
}

.dropdown .user {
  padding: 2% 0;
  
}
.dropdown .user .username span{
  margin-left: 5px;
  opacity: 0.8;
  cursor: pointer;
}
.dropdown .user .username span:hover{
  opacity:1;

}
.dropdown .user .username a{
  color: #fff;
}

.dropdown .nav-list {
 

}
.dropdown .nav-list .item a{
  color: rgb(255, 255, 255);
}
.dropdown .nav-list .item{
  cursor: pointer;
  color: rgb(255, 255, 255);
  transition: all 0.12s ease-in-out 0s;
  text-align: left;
  min-height: 32px;
  border-radius: 6px;
  text-decoration: none;
  font-size: 14px;
  line-height: 19.32px;
  font-weight: 700;
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 100%;
}

.dropdown .nav-list .item:hover{
  background-color: rgba(255, 255, 255, 0.05);
  color: rgb(255, 255, 255);
}

.dropdown .divider{
  background-color: rgba(255, 255, 255, 0.1);
  height: 1px;
  padding-top: 0px;
  padding-bottom: 0px;
  margin-top: 12px;
  margin-bottom: 12px;
}

.menu {
  width: 100%;
}

.menu-item {
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
  transition: background var(--speed);
  padding: 0.5rem;
}

.menu-item .icon-button {
  margin-right: 0.5rem;
}

.menu-item .icon-button:hover {
  filter: none;
}

.menu-item:hover {
  /* background-color: #525357; */
}

.icon-right {
  margin-left: auto;
}

.menu-primary-enter {
  position: absolute;
  transform: translateX(-110%);
}

.menu-primary-enter-active {
  transform: translateX(0%);
  transition: all var(--speed) ease;
}

.menu-primary-exit {
  position: absolute;
}

.menu-primary-exit-active {
  transform: translateX(-110%);
  transition: all var(--speed) ease;
}

.menu-secondary-enter {
  transform: translateX(110%);
}

.menu-secondary-enter-active {
  transform: translateX(0%);
  transition: all var(--speed) ease;
}

.menu-secondary-exit {}

.menu-secondary-exit-active {
  transform: translateX(110%);
  transition: all var(--speed) ease;
}

