.card-pack {
    margin: 0 auto;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(4, minmax(300px, 1fr));
    padding: 2% 2%;
}


.creators-galllary{
    margin: 0 auto;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(3, minmax(300px, 1fr));
}

.artworks-galllary{
    margin: 0 auto;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(3, minmax(300px, 1fr));
}

@media screen and (min-width: 52em) {
   
 }

@media screen and (min-width: 92em) {
    .card-pack{
        width: 80%;
        margin: 0 auto;
    }
    .home-creators{
        width: 90%;
        margin: 0 auto;
        padding: 2% 2%;

    }
    .home-artworks{
        width: 90%;
        margin: 0 auto;
        padding: 2% 2%;

    }

    .creators-galllary{
        grid-template-columns: repeat(6, minmax(300px, 1fr));
    }
    .artworks-galllary{
        grid-template-columns: repeat(4, minmax(300px, 1fr));
    }


 }