
.search-container {
    margin: 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    -webkit-box-flex: 1;
    margin-right: 24px;
  }
  
  .search-container .search-dropdown {
    z-index: 500;
    visibility: visible;
    position: absolute;
    inset: 0px auto auto 0px;
    margin: 0px;
    transform: translate(96px, 80px);
    display: flex;
    -webkit-box-flex: 1;
    flex-grow: 1;
    flex-direction: column;
  }
  .search-container .inside-container {
    display: flex;
    -webkit-box-flex: 1;
    flex-grow: 1;
    flex-direction: column;
  }
  
  .search-container .inside-container form {
    margin: 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    flex-direction: row;
    -webkit-box-flex: 1;
    flex-grow: 1;
    -webkit-box-align: stretch;
    align-items: stretch;
    border: 1px solid transparent;
    background: rgba(255, 255, 255, 0.07);
    height: 40px;
    padding: 0px 8px 0px 16px;
    border-radius: 24px;
    transition: all 0.12s ease-in-out 0s;
  }
  
  .search-icon-container {
    margin: 0px;
    padding: 0px;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    flex-basis: 20px;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
  }
  
  .search-icon-container span {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.4);
    transition: all 0.12s ease-in-out 0s;
  }
  
  .search-icon-container .search-icon {
    margin: 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    width: 14px;
    height: 14px;
  }
  
  #search-input {
    margin: 0px 0px 0px 8px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    -webkit-box-flex: 1;
    -webkit-box-pack: center;
    justify-content: center;
  }
  
  .search-container .search-input {
    font-weight: bold;
    width: 100%;
    appearance: textfield;
    outline-offset: -2px;
    font-size: 15px;
    background-color: rgba(0, 0, 0, 0);
    color: rgba(255, 255, 255, 0.8);
    border-width: 0px;
    overflow: hidden;
    outline: none;
  }
  
  .search-btn {
    display: none;
  }